<script setup>
import { RouterLink } from 'vue-router';
import { onMounted } from 'vue';
import { gsap } from 'gsap';

const GST = (repeat, repeatDelay) => gsap.timeline({ repeat, repeatDelay });

onMounted(() => {
  // bg animation
  const bg = GST(-1, 3);

  bg.to('#bg', {
    transformOrigin: '50% 50%',
    scale: 1.2,
    duration: 3,
  }).to('#bg', {
    transformOrigin: '50% 50%',
    scale: 1,
    duration: 3,
  });

  // gears
  const gearSmall = GST(-1, 0);

  gearSmall.to('#gear-2', {
    transformOrigin: '50% 50%',
    rotate: 360,
    duration: 2,
    ease: 'none',
  });

  const gearBig = GST(-1, 0);

  gearBig.to('#gear-1', {
    transformOrigin: '50% 50%',
    rotate: -360,
    duration: 2,
    ease: 'none',
  });

  // window-2
  const windowTwo = GST(-1, 5);
  windowTwo.to('#window-2', {
    translateX: -100,
    translateY: 325,
    duration: 2,
    ease: 'none',
  }).to('#window-2', {
    translateX: 0,
    translateY: 0,
    duration: 2,
    ease: 'none',
  });

  // arrow
  const arrowBlock = GST(-1, 0);
  arrowBlock.to('#arrow', {
    tranformOrigin: '50% 50%',
    rotate: 5,
    duration: 1,
  }).to('#arrow', {
    tranformOrigin: '50% 50%',
    rotate: 0,
    duration: 1,
  }).to('#arrow', {
    tranformOrigin: '50% 50%',
    rotate: -5,
    duration: 1,
  }).to('#arrow', {
    tranformOrigin: '50% 50%',
    rotate: 0,
    duration: 1,
  });

  // play-button
  const playButton = GST(-1, 8);

  playButton.to('#play-button', {
    transformOrigin: '50% 50%',
    scale: 0.7,
    duration: 0.1,
    delay: 0,
  }).to('#play-button', {
    transformOrigin: '50% 50%',
    scale: 1,
    duration: 0.1,
  });
});
</script>
