import { gsap } from 'gsap';

const GST = (repeat, repeatDelay) => gsap.timeline({ repeat, repeatDelay });

onMounted(() => {
  // bg animation
  const bg = GST(-1, 3);

  bg.to('#bg', {
    transformOrigin: '50% 50%',
    scale: 1.2,
    duration: 3,
  }).to('#bg', {
    transformOrigin: '50% 50%',
    scale: 1,
    duration: 3,
  });

  // gears
  const gearSmall = GST(-1, 0);

  gearSmall.to('#gear-2', {
    transformOrigin: '50% 50%',
    rotate: 360,
    duration: 2,
    ease: 'none',
  });

  const gearBig = GST(-1, 0);

  gearBig.to('#gear-1', {
    transformOrigin: '50% 50%',
    rotate: -360,
    duration: 2,
    ease: 'none',
  });

  // target
  const circleTwo = GST(-1, 1);

  circleTwo.to('#circle-2', {
    transformOrigin: '50% 50%',
    scale: 0.9,
    duration: 4,
    delay: 0,
  }).to('#circle-2', {
    transformOrigin: '50% 50%',
    scale: 1,
    duration: 4,
  });

  const circleThree = GST(-1, 1);

  circleThree.to('#circle-3', {
    transformOrigin: '50% 50%',
    scale: 1.2,
    duration: 4,
    delay: 0,
  }).to('#circle-3', {
    transformOrigin: '50% 50%',
    scale: 1,
    duration: 4,
  });

  // arrow
  const arrow = GST(-1, 4);

  arrow.to('#arrow', {
    transformOrigin: '100% 0',
    scale: 0.7,
    duration: 0.1,
    delay: 6,
  }).to('#arrow', {
    transformOrigin: '100% 0',
    scale: 1,
    duration: 0.1,
  });
});
</script>
